<template>
<el-card class="news_card">
    <div class="news_loading" v-if="isLoading"></div>
    <div v-if="!isLoading">
        <news-item v-for="item in unreadedNews" :key="item.id" :newsinfo="item"></news-item>
        <p class="news_title">{{this.$t('news.messages')}}</p>
        <el-divider></el-divider>
        <news-item v-for="item in readedNews" :key="item.id" :newsinfo="item"></news-item>
    </div>
</el-card>
</template>

<script>
import {
    mapActions,
    mapState
} from 'vuex';
import {
    startLoading,
    endLoading
} from '@/util/loading.js';
import NewsItem from './components/NewsItem.vue';
export default {
    mounted() {
        startLoading('.news_loading');
        this.getReadedWithdrawalInfo({
            shop_id: this.shopId,
        }).then(() => {
            this.isLoading = false;
            endLoading();
        });
        this.getUnreadedWithdrawalInfo({
            shop_id: this.shopId,
        }).then(() => {
            this.isLoading = false;
            endLoading();
        });
    },
    data() {
        return {
            isLoading: true,
        };
    },
    methods: {
        ...mapActions({
            getReadedWithdrawalInfo: 'bill/sGetReadWithdrawalInfo',
            getUnreadedWithdrawalInfo: 'bill/sGetUnreadWithdrawalInfo',
        }),
    },
    computed: {
        ...mapState({
            shopId: (state) => state.user.data.role_id,
            readedNews: (state) => state.bill.readedWithdrawalList,
            unreadedNews: (state) => state.bill.unreadedWithdrawalList,
        }),
    },
    components: {
        NewsItem,
    },
};
</script>

<style lang="scss" scoped>
.news_loading {
    height: 200px;
}

.news_card {
    max-height: calc(100vh - 8em);
    overflow-y: auto;
}

.news_title {
    color: #dcdfe6;
    text-align: center;
    padding-top: 1em;
}
</style>
