<template>
<el-card>
    <el-row :gutter="10">
        <el-col :span="18">
            <p class="title_type">{{ title }}</p>
        </el-col>
        <el-col :span="6">{{ time }}</el-col>
    </el-row>
    <el-divider></el-divider>
    <div class="detail">
        <p>
            {{ content }}
        </p>
        <div class="right_type">
            <el-button type="primary" plain class="view_btn" @click="veiwDetail">{{
          this.$t('news.detail')
        }}</el-button>
        </div>
        <news-dialog v-if="false"></news-dialog>
    </div>
</el-card>
</template>

<script>
import {
    mapActions,
    mapMutations
} from 'vuex';
import NewsDialog from './NewsDialog.vue';
export default {
    props: ['newsinfo'],
    methods: {
        veiwDetail() {
            // 未读调用读取得接口

            const h = this.$createElement;
            this.$msgbox({
                message: h(NewsDialog, {
                    props: {
                        info: this.newsinfo,
                        parent: this,
                        title: this.title,
                        time: this.time,
                    },
                }),
                confirmButtonText: this.$t('btn.confirm'),
            }).then(() => {
                if (this.newsinfo.success && !this.newsinfo.view) {
                    this.changeReadList([this.newsinfo]);
                    this.readWithdrawalInfo({
                        id: this.newsinfo.id,
                    });
                }
            });
        },
        ...mapActions({
            readWithdrawalInfo: 'bill/sReadWithdrawalInfo',
        }),
        ...mapMutations({
            changeReadList: 'bill/CHANGE_WITHDRAWAL_READ_STATUS',
        }),
    },
    computed: {
        title() {
            let headTitle = null;
            // 提交申请
            if (this.newsinfo.success) {
                headTitle = this.$t('news.successTitle');
            } else {
                headTitle = this.$t('news.applyTitle');
            }
            return headTitle;
        },
        content() {
            let dialogContent = null;
            if (this.newsinfo.success) {
                dialogContent = this.$t('news.successMsg');
            } else {
                dialogContent = this.$t('news.applyMsg');
            }
            return dialogContent;
        },
        time() {
            let timeInfo = this.newsinfo.success ?
                this.$d(new Date(this.newsinfo.success_time), 'long') :
                this.$d(new Date(this.newsinfo.withdrawl_time), 'long');
            return timeInfo;
        },
    },
    components: {
        NewsDialog,
    },
};
</script>

<style lang="scss" scoped>
.title_type {
    color: $--color-primary;
}

.right_type {
    text-align: right;

    .view_btn {
        width: 130px;
        line-height: 1;
    }
}
</style>
